@import '../../Variables.scss';
.MainButton {
  position: relative;
  width: fit-content;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  font-weight: 500;
  height: 40px;
  padding: 0 15px;
  background-color: $color-primary;
  box-shadow: 0px 4px 10px rgba(63, 154, 85, 0.5);
  color: white;
  border-radius: 4px;
  cursor: pointer;
  user-select: none;
}
