@import '../../../Variables.scss';
.DesignForm {
  position: relative;
  width: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.formNavWrap {
  width: 30%;
}

.formInputWrap {
  width: 65%;
  background-color: white;
  border-radius: 8px;
  border: 1px solid $color-border-gray;
  padding: 40px 0;
}
.formContainer {
  width: 80%;
  margin: auto;
}
.buttonWrap {
  display: flex;
  justify-content: space-between;
  margin-top: 40px;
}
.formButton {
  width: 100px;
  &.hide {
    visibility: hidden;
  }
}
.submitButton {
  width: 100%;
  &.inactive {
    opacity: 0.5;
  }
  margin-bottom: 20px;
}

@media (max-width: 765px) {
  .DesignForm {
    flex-direction: column;
  }
  .formNavWrap {
    // display: none;
    width: 100%;
  }
  .formInputWrap {
    width: 100%;
  }
}
