@import '../../Variables.scss';

.ProjectDesign {
  position: relative;
  width: 100%;
  overflow-x: hidden;
  background-color: $color-gray;
  min-height: 100vh;
}

.banner {
  width: 100%;
  height: 100%;
  background-blend-mode: hard-light;
  background-position: right top;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: $color-primary;
  .bannerContainer {
    text-align: left;
    h2 {
      font-weight: 900;
      font-size: 72px;
      line-height: 80px;
      color: #ffffff;
      text-shadow: 0px 0px 10px rgba(255, 255, 255, 0.75);
      margin: 0;
      word-spacing: 100vw;
      word-break: break-word;
    }
    h3 {
      font-weight: bold;
      font-size: 24px;
      line-height: 60px;
      color: #ffffff;
      margin: 0;
    }
  }
}
