@import '../../Variables.scss';
.Projects {
  position: relative;
  width: 100%;
  min-height: 100vh;
  overflow-x: hidden;
  background-color: $color-gray;
}

.introWrap {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  width: 100%;
  .textWrap {
    width: 60%;
    text-align: left;
    h2 {
      font-weight: 900;
      font-size: 36px;
    }
    p {
      font-weight: 500;
      font-size: 18px;
    }
  }
  .linkWrap {
    width: 30%;
    display: flex;
    flex-direction: column;
    align-items: center;

    .link {
      color: $color-primary;
      font-weight: 900;
      font-size: 24px;
      &:hover {
        color: $color-primary;
      }
    }
    img {
      width: 150px;
      margin-top: 30px;
    }
  }
}

.projectListWrap {
  // position: relative;
  // width: 100%;
}

.tabWrap {
  position: relative;
  width: 100%;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 30px;
  .tab {
    margin: 0 20px;
    height: 100%;
    display: flex;
    align-items: center;
    cursor: pointer;
    h2 {
      font-weight: bold;
      font-size: 18px;
      margin: 0;
    }
    &.active,
    &:hover {
      h2 {
        color: $color-primary;
      }
      border-bottom: 5px solid rgba(63, 154, 85, 0.2);
      border-top: 5px solid transparent;
    }
  }
}

@media(max-width: 765px){
  .introWrap {
    flex-direction: column;
    .textWrap {
      width: 100%;
      text-align: left;
      h2 {
        font-size: 25px;
      }
      p {
        font-size: 16px;
      }
    }
    .linkWrap {
      width: 100%;
      img {
        width: 100px;
        margin-top: 20px;
      }
    }
  }
}