@import '../../Variables.scss';
.Header {
  position: relative;
  width: 100%;
  height: 100px;
  background-color: white;
  overflow: hidden;
}
.headerContainer {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0;

  .logo {
    width: 250px;
  }

  .menuWrap {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 100%;
    flex-grow: 1;
    .navLinkWrap {
      height: 100%;
      display: flex;
      align-items: center;
      margin-left: 50px;
      .navLink {
        margin-right: 50px;
        height: 100%;
        box-sizing: border-box;
        display: flex;
        align-items: center;
        h2 {
          font-weight: bold;
          font-size: 18px;
          margin: 0;
        }
        &.navLinkActive,
        &:hover {
          h2 {
            color: $color-primary;
          }
          border-bottom: 5px solid rgba(63, 154, 85, 0.2);
          border-top: 5px solid transparent;
        }
      }
    }
    .connectButton {
      height: 40px;
    }
  }

  .langButton {
    height: 40px;
    border: 1px solid $color-primary;
    box-sizing: border-box;
    filter: drop-shadow(0px 4px 10px rgba(63, 154, 85, 0.5));
    border-radius: 4px;
    line-height: 40px;
    width: 100px;
    text-align: center;
    padding: 0 10px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    color: $color-primary;
    font-weight: bold;
    margin-left: 20px;
    cursor: pointer;
    user-select: none;
  }

  .hamburger {
    display: none;
  }
}

.walletModalWrapper {
  :global(.ant-modal-content) {
    box-sizing: border-box;
    background: white;
    border: 1px;
    border-radius: 10px;
    padding: 0;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    font-family: 'Battambang', sans-serif;
  }
  :global(.ant-modal-body) {
    padding: 15px 20px;
  }
}

@media (max-width: 765px) {
  .Header {
    position: fixed;
    height: 60px;
    z-index: 2;
  }
  .headerContainer {
    width: 100%;
    padding: 0 10px;
    position: relative;
    .logo {
      width: auto;
      height: 20px;
    }

    .hamburger {
      display: block;
      font-size: 20px;
      flex-grow: 2;
      text-align: right;
    }

    .menuWrap {
      flex-direction: column;
      position: fixed;
      top: 60px;
      right: 0;
      z-index: 10;
      height: auto;
      width: auto;
      background-color: white;
      border-radius: 0 0 5px 5px;
      flex-grow: 0;
      padding: 20px;
      .navLinkWrap {
        flex-direction: column;
        margin: 0;
        .navLink {
          height: 100%;
          box-sizing: border-box;
          display: flex;
          align-items: center;
          justify-content: center;
          margin: 0;
          h2 {
            text-align: center;
            font-weight: bold;
            font-size: 18px;
            margin: 0;
          }
          &.navLinkActive,
          &:hover {
            h2 {
              color: $color-primary;
            }
            border-bottom: 5px solid rgba(63, 154, 85, 0.2);
            border-top: 5px solid transparent;
          }
        }
      }
      .connectButton {
        margin-top: 20px;
        height: 30px;
      }
    }
  }
}
