@import '../../Variables.scss';

.walletInfoWrapper{
  border: 1px solid #F2F2F2;
  box-sizing: border-box;
  padding: 20px;
  margin-bottom: 20px;
  h3{
    color: $color-text-gray;
    font-size: 15px;
    font-weight: bold;
  }
  .walletAddressWrapper{
    display: flex;
    align-items: center;
    justify-content: space-between;

    .address{
      width: calc(100% - 60px);
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      background: rgba(63, 154, 85, 0.1);
      border-radius: 10px;
      padding: 10px 10px;
      color: $color-primary;
      font-weight: bold;
      font-size: 15px;
      letter-spacing: 0.5px;
    }
    .copyButton{
      color: $color-primary;
      font-weight: bold;
      cursor: pointer;
    }
  }

  .balanceRow{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 10px 0;
    .tokenName{
      display: flex;
      align-items: center;
      justify-content: flex-start;
      color: $color-text-gray;
      font-weight: bold;
      img{
        width: 20px;
        margin-right: 10px;
      }
    }
    .tokenBalance{
      color: #12162D;
      font-weight: bold;
    }
  }

}

.bscscanLink{
  a{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    color: $color-primary;
    font-weight: bold;
    img{
      width: 20px;
      margin-left: 10px;
    }
  }
}

.disconnectButton{
  margin-top: 20px;
  text-align: center;
  color: $color-primary;
  border: 2px solid $color-primary;
  font-weight: bold;
  padding: 10px 0;

  &:hover{
    background-color: $color-primary;
    color: white;
    cursor: pointer;
  }
}













