@import '../../Variables.scss';
.popup {
  position: relative;

  .title {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 2%;

    p {
      margin: 2% 0;
      font-size: 18px;
      font-weight: bold;
    }

    img {
      cursor: pointer;
      width: 12px;
      height: 12px;
    }
  }

  .walletCard {
    background-color: white;
    border-radius: 10px;
    padding: 0 2%;
    display: flex;
    cursor: pointer;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 10px;
    border: 1px $color-primary solid;

    p {
      padding-left: 5%;
      margin: 10px 0;
      color: $color-primary;
      font-weight: 600;
    }
    img {
      width: 60px;
      height: 60px;
      margin-right: 20px;
    }
  }
  .walletCard:hover {
    background-color:$color-primary;
    p{
      color: white;
    }
  }
  .closeIcon {
    width: 16px;
    height: 16px;
    margin-left: 100%;
    padding-right: 50px;
    cursor: pointer;
  }
  .accountTitle {
    font-size: 21px;
    font-weight: 500;
    padding: 2%;
    p {
      margin-top: 0;
    }

    // margin: 0;
  }

  .disconnectButton {
    width: 115px;
    height: 35px;
    vertical-align: middle;
    line-height: 35px;
    background-color: lightgray;
    border-radius: 60px;
    margin: auto;
    cursor: pointer;
    p {
      width: 100%;
      text-align: center;
    }
  }
}

