@import '../../Variables.scss';

.Home {
  position: relative;
  width: 100%;
  overflow-x: hidden;
}

.section {
  position: relative;
  width: 100%;
}

.gray {
  background-color: $color-gray;
}

.green {
  background-color: $color-primary;
}

.assorted {
  background: linear-gradient(white 50%, $color-primary 50%);
}

.introContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  .introText {
    text-align: left;
    width: 50%;
    h2 {
      font-weight: 900;
      font-size: 56px;
      line-height: 60px;
    }
    p {
      font-weight: 500;
      font-size: 18px;
    }
    .projectButton {
      margin-top: 50px;
    }
  }
  .introImg {
    width: 45%;
  }
}

.target {
  position: relative;
  width: 80%;
  margin:auto auto 50px;
  background-color: $color-primary;
  border-radius: 24px;
  &::before {
    content: '';
    position: absolute;
    top: 5%;
    bottom: 5%;
    left: 5%;
    right: 5%;
    border-radius: 24px;
    z-index: -1;
    box-shadow: rgba(63, 154, 85, 0.5) 0 35px 0px 0px;
  }
  &::after {
    content: '';
    position: absolute;
    top: 10%;
    bottom: 10%;
    left: 10%;
    right: 10%;
    border-radius: 24px;
    z-index: -1;
    box-shadow: rgba(63, 154, 85, 0.5) 0 70px 0px 0px;
  }

  .targetBgFigure {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 25%;
    transform: translate(35%, 15%);
  }

  .targetText {
    position: relative;
    text-align: left;
    padding: 30px;

    h2 {
      color: white;
      margin-left: 20px;
      margin-bottom: 0;
      font-weight: 900;
      font-size: 36px;
      text-shadow: 0px 1px 2px rgba(255, 255, 255, 0.7);
    }
    p {
      color: white;
      display: flex;
      align-items: center;
      img {
        width: 50px;
        margin-right: 20px;
      }
      span {
        font-size: 24px;
        line-height: 24px;
        font-weight: 500;
      }
    }
  }
}

.link {
  white-space: nowrap;
  color: $color-primary;
  font-weight: 900;
  font-size: 24px;
  &:hover {
    color: $color-primary;
  }
}

.projectContainer {
  h2 {
    font-weight: 900;
    font-size: 36px;
    text-align: left;
  }
}

.reasonContainer {
  position: relative;
  .reasonWrap {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 75%;
    text-align: left;
    height: fit-content;
    z-index: 1;
    h2 {
      font-weight: 700;
      font-size: 56px;
      line-height: 60px;
      color: white;
      margin-bottom: 0;
    }
    .vl {
      border-left: 4px solid white;
      align-self: stretch;
      margin: 0 30px;
    }
    .reasonText {
      color: white;

      p {
        margin-bottom: 20px;
        font-size: 16px;
      }
    }
  }
  .reasonFigure {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 20%;
  }
}

.daoIntroContainer {
  h2 {
    font-weight: 900;
    font-size: 36px;
    line-height: 40px;
  }
  p {
    font-size: 20px;
    line-height: 30px;
    font-weight: 400;
  }
  .daoIntroCardWrap {
    position: relative;
    width: 100%;
    padding: 50px;
    .daoIntroCard {
      position: relative;
      width: 100%;
      padding: 24px;
      background: linear-gradient(225.78deg, #fbfbfb 0%, #ffffff 98.68%);
      box-shadow: 0px 20px 25px -5px rgba(25, 25, 25, 0.16);
      border-radius: 4px;
      img {
        width: 56px;
        margin-bottom: 15px;
      }
      h2 {
        font-size: 20px;
        font-weight: 700;
      }
      p {
        font-size: 16px;
        line-height: 24px;
        font-weight: 400;
        margin: 0;
      }
    }
  }
}

.faqContainer {
  background-color: $color-gray;
  h2 {
    font-weight: 800;
    font-size: 36px;
  }
  .customCollapse {
    width: 100%;
    margin: 50px 0;
    border-radius: 8px;
    border: 1px solid rgba(0, 0, 0, 0.1);
    background-color: transparent;
    // :global(.ant-collapse-item-active) {
    //   :global(.ant-collapse-content) {
    //     background-color: rgba(63, 154, 85, 0.2);
    //   }
    //   :global(.ant-collapse-header) {
    //     background-color: rgba(63, 154, 85, 0.2);
    //   }
    // }
    .customPanel {
      text-align: left;
      p {
        span {
          color: rgb(18, 180, 199);
          font-weight: 600;
          cursor: pointer;
        }
      }

      &:first-child {
        border-top-left-radius: 8px;
        border-top-right-radius: 8px;
      }
      &:last-child {
        border-bottom-left-radius: 8px;
        border-bottom-right-radius: 8px;
      }
      background-color: $color-gray;
      border: 1px solid rgba(0, 0, 0, 0.1);
      :global(.ant-collapse-header) {
        // background-color: $color-primary;
        font-weight: 700;
        font-size: 20px;
      }
      :global(.ant-collapse-content) {
        background-color: rgba(63, 154, 85, 0.2);
        font-weight: normal;
        font-size: 16px;
      }
    }
  }
}

@media (max-width: 765px) {
  .introContainer {
    flex-direction: column;

    .introText {
      width: 100%;

      h2 {
        font-size: 36px;
        line-height: 40px;
      }
      p {
        font-size: 16px;
        line-height: 20px;
      }
    }
    .introImg {
      width: 65%;
      margin-top: 30px;
    }
  }
  .target {
    position: relative;
    width: 100%;

    .targetText {
      padding: 20px;

      h2 {
        font-size: 24px;
      }
      p {
        img {
          width: 25px;
          margin-right: 10px;
        }
        span {
          font-size: 16px;
          line-height: 18px;
        }
      }
    }
  }
  .link {
    font-size: 20px;
  }

  .projectContainer {
    h2 {
      font-size: 24px;
    }
  }

  .reasonContainer {
    .reasonWrap {
      flex-direction: column;
      width: 100%;
      h2 {
        font-size: 36px;
        line-height: 40px;
      }
      .vl {
        border-left: none;
        border-top: 4px solid white;
        align-self: stretch;
        margin: 30px 0;
      }
      .reasonText {
        p {
          margin-bottom: 10px;
        }
      }
    }
    .reasonFigure {
      position: absolute;
      bottom: 0;
      right: 0;
      width: 30%;
    }
  }

  .daoIntroContainer {
    p {
      font-size: 16px;
      line-height: 18px;
    }
    .daoIntroCardWrap {
      padding: 20px;
    }
  }

  .faqContainer {
    h2 {
      font-weight: 800;
      font-size: 36px;
    }
    .customCollapse {
      .customPanel {
        :global(.ant-collapse-header) {
          font-size: 16px;
          line-height: 22px;
        }
        :global(.ant-collapse-content) {
          font-size: 14px;
          line-height: 20px;
        }
      }
    }
  }
}
